import mapComponent from "@/components/mapComponent";
export default {
  name: "dealDetailCard",
  props: ['info', 'visible', 'qrcodeValue'],
  components: {
    mapComponent
  },
  methods: {
    close() {
      this.$emit('close');
    },

    beforeClose(done) {
      this.$emit('close');
      done();
    },

    readMoreDeal(userId, identity, companyId) {
      this.$emit('viewProfile', userId, identity, companyId);
    },

    share(info) {
      this.$emit('share', info);
    }

  }
};